import Form from "react-bootstrap/Form";
import InputGroup from "react-bootstrap/InputGroup";
import Button from "react-bootstrap/Button";
import React, {useContext, useState} from "react";
import {newCommentsRequest} from "./request/newCommentsRequest";
import AuthContext from "../../../store/context/auth-context";
import {useLocation, useNavigate} from "react-router-dom";

export default function NewComments(props: any) {
    const authCtx = useContext(AuthContext);
    const [content, setContent] = useState('');
    const navigate = useNavigate();
    const location = useLocation();


    const commentsContentChangeHandler = (event : any) => {
        setContent(event.currentTarget.value);
    };

    const contentCheckHandler = () => {
        if (content.trim().length < 10) {
            alert("댓글은 10글자 이상 입력해주세요!")
            return false
        }
        return true
    }

    const postCommentHandler = async () => {
        const params = {
            boardsId: props.boardsId,
            commentsContent: content.trim(),
        }
        if (!contentCheckHandler()) { return; }
        const response = await newCommentsRequest(params, authCtx.authorization);
        if (response?.status === 200) {
            alert("댓글 작성을 완료하였습니다!!")
            setContent('');
            props.setRefresh(!props.refresh)
            props.setCommentsData([])
            props.setCommentsId(null)
        }
    }

    return (
        <Form role={"submit"}>
            <InputGroup className="mb-5">
                <Form.Control as="textarea" value={content} onChange={commentsContentChangeHandler} rows={3}/>
                <Button variant="outline-secondary" id="button-addon1" onClick={postCommentHandler}>댓글 쓰기</Button>
            </InputGroup>
        </Form>
    )
}
