import axios from "axios";

export interface newBoardParams {
    boardCategory : string;
    boardTitle : string | null;
    boardContent : string | null;
};

export const newBoardRequest = (params : newBoardParams, token : string) => {
    let url;
    url = "/boards"
    axios.post(url, {
        boardCategory: params.boardCategory,
        title: params.boardTitle,
        content: params.boardContent
    }, {
        headers: {
            authorization: token,
        }
    }).then(function (response) {
        if (response.status === 200) {
            alert("게시물 작성을 완료하였습니다!");
        } else {
            alert(response.data.message);
        }
    }).catch(function (error) {
        alert(error.response.data.message);
    });
}
